.logo {
  width: 40px;
  height: 40px;
  display: flex;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-inline-start: 50px;
  display: flex;
  overflow: hidden;
  box-shadow: 0 2px 4px #0000000d;
}

.nav__list {
  align-items: center;
  margin: 0 50px;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active, .nav__link:hover {
  color: #00acdc;
}

.main {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 48px 46px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    --column-count: 2;
    width: 100%;
  }
}

@media (width >= 768px) {
  .main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-count: 4;
  }
}

.product-card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .2s ease-in-out;
}

.product-card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.product-card__image-content {
  width: 100%;
  height: 100%;
}

.product-card__name {
  color: #060b35;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product-card__code {
  color: #616070;
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product-card__reviews {
  align-items: flex-end;
  margin-bottom: 24px;
  display: flex;
}

.product-card__price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.product-card__price-label {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.product-card__price-sum {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product-card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 39px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card:hover .product-card__name {
  color: #34568b;
}

.reviews_count {
  margin-left: auto;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, Arial, sans-serif;
  font-weight: 500;
}

body {
  margin: 0;
}
